<template>
  <div id="shareIt" style="z-index: 12">
    <!-- 在微信环境下弹出分享引导层 -->
     <div v-if="wechatisshow" style="z-index: 3" class="wechat-cotent"  @click = 'closeShare'>
        <div class="">
          <h1 class="step1">
            第一步：点击右上角的...
            <img src="../../assets/img/dot.png" class="dot-img" alt="">
          </h1>
          <div class="jiantou">
            <img class="wp100" src="../../assets/img/jiantou.png" alt="">
          </div>
          <div class="imgIcon">
           <h1 class="step2">第二步：选择分享方式进行分享</h1>
           <div class="wechat-icon">
             <div class="wechat-icon-img">
              <img src="../../assets/img/friend.png" class="wp100">
             </div>
             <div class="wechat-icon-img">
              <img src="../../assets/img/wechat.png" class="wp100" alt="">
             </div>
             <div class="wechat-icon-img">
              <img src="../../assets/img/qq.png" class="wp100" alt="">
             </div>
           </div>
          </div>
        </div>
     </div>
     <!-- 不在微信环境下，但是在APP环境下,引导分享的方式 -->
      <div class='appShare' v-if="$config.source_style >= 6" @click='closeShare'>
        <transition name='disappear'>
          <div class="app-content" v-if="appisshow">
            <div class="shareWayTop">
              <div class="viewbtn" @click="jumpgetfanpageBtn"></div>
            </div>
            <div class="topshow">
              <div class="shareWay">
                <!-- 锁粉神器 -->
                <div class="icon" @click.stop="sendSystem('system')">
                  <div class="iconBox">
                    <div class="iconImg">
                      <img src="../../assets/img/getfan.png" alt="锁粉神器">
                      <div class="recommend_tag"></div>
                    </div>
                    <div class="name">锁粉神器</div>
                  </div>
                </div>
                <div class="icon" @click.stop="setShare('sinaweibo')">
                  <div class="iconBox">
                    <div class="iconImg">
                      <img src="../../assets/img/weibo.png" alt="">
                    </div>
                    <div class="name">微博</div>
                  </div>
                </div>
                <div class="icon" @click.stop="setShare('qq')">
                  <div class="iconBox">
                    <div class="iconImg">
                      <img src="../../assets/img/qq.png" alt="QQ">
                    </div>
                    <div class="name">QQ好友</div>
                  </div>
                </div>
                <div class="icon" @click.stop="setShare('weixin','WXSceneSession')">
                  <div class="iconBox">
                    <div class="iconImg">
                    <img src="../../assets/img/wechat.png" alt="微信">
                    </div>
                    <div class="name">微信好友</div>
                  </div>
                </div>
                <div class="icon" >
                  <div class="iconBox copybutton" @click="getcopyLinkbtn"  :data-clipboard-text="copyLink">
                    <div class="iconImg">
                      <img src="../../assets/img/copybtn.png" alt="复制链接">
                    </div>
                    <div class="name">复制链接</div>
                  </div>
                </div>
                <!-- <div class="icon" @click="sendMsg">
                  <div class="iconBox copybutton">
                    <div class="iconImg">
                      <img src="../../assets/img/msgshare.png" alt="短信分享">
                    </div>
                    <div class="name">短信分享</div>
                  </div>
                </div> -->
              </div>
              <div class="canlehid" @click="closeShare">取消</div>
            </div>
          </div>
        </transition>
      </div>
  </div>
</template>

<script>
// 导入wx 分享接口对象
import wx from 'weixin-js-sdk'
import Clipboard from 'clipboard'
import { mapState } from 'vuex'
export default {
  name: 'shareIt',
  props: {
    details: {
      type: Object,
      default: null,
      copyflag: true
    },
    type: {
      type: String
    }
  },
  data () {
    return {
      isShowShare: true,
      appisshow: false, // 控制在APP环境下要不要分享到朋友圈，微信，QQ
      wechatisshow: false, // 控制微信环境下要不要分享到朋友圈，微信，QQ
      pageUrl: '', // 提交给服务器端的URL
      share: null,
      copyLink: '', // 复制链接
      setdomain: '' // 配置中转页域名
    }
  },
  computed: mapState({
    commonState: state => state.common,
    comment: state => state.comment,
    login_bol: state => state.login_bol
  }),
  methods: {
    // 分享功能
    /** 实现思路：
     * 1.检测有没有登录，如果没有登录，要先登录，然后再返回到当前页，如果已经登录，检测是否在微信环境
     * 2.如果在微信环境中，直接浮层引导分享，（可分享，微信群，朋友圈，QQ）
     * 3.如果非微信环境，直接将带分享人信息参数的文章分享页短连接，复制到剪贴板上，
     * 格式如下：文章标题前15个字：http://t.cn/fefvDAA 带分享人信息参数，并弹出toast提示："本文链接已经复制，快发送给好友吧"
     * 4.分享后，分享方案和描述如下：标题为文章标题，内容为文章内容，同时最前方加入【掘金宝】标识，图片为文章首图(缺省图片为logo)
     *  */
    jumpgetfanpageBtn () {
      this.$router.push({name: 'LockfanPage'})
    },
    shareSuccess () {
      this.$emit('shareSuccess')
    },
    // 发送短信
    sendMsg () {
      let msg = window.plus.messaging.createMessage(window.plus.messaging.TYPE_SMS)
      msg.to = ['10086']
      msg.bodyType = 'text'
      if (this.type === 'jjb_small_video') {
        msg.body = `${this.details.title}↓↓↓点击查看↓↓↓${this.$config.currentWebUrl}?path=${this.$route.path}&inviteCode=${this.$store.state.common.userInfo.invitation}`
      } else {
        msg.body = `${this.details.title}↓↓↓点击查看↓↓↓${this.$config.currentWebUrl}?path=${this.$route.path}&inviteCode=${this.$store.state.common.userInfo.invitation}`
      }
      window.plus.messaging.sendMessage(msg)
    },
    // 分享功能
    shareComment () {
      let weChatFlag = this.$utils.isWx()
      if (weChatFlag) {
        // 让分享引导浮层弹出来
        this.wechatisshow = true
      } else { // 如果不在微信环境下
        this.appisshow = true
      }
    },
    // 系统分享
    sendSystem () {
      this.$utils.plusReady(plus => {
        let msg = {
          type: 'text',
          content: `${this.details.title}↓↓↓点击查看↓↓↓${this.$config.currentWebUrl}?path=${this.$route.path}&inviteCode=${this.$store.state.common.userInfo.invitation}`
        }
        plus.share.sendWithSystem(msg, () => {
          this.closeShare()
        }, function () {
          this.closeShare()
        })
      })
    },
    // 分享朋友圈
    setShare (id, ext) {
      // 文章詳情分享統計埋點需求
      let that = this
      let shareUrl = this.$config.currentWebUrl + '?path=' + this.$route.path + '&inviteCode=' + this.$utils.cache('inviteCode')
      let msg = {
        thumbs: [`https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}`],
        href: shareUrl
      }
      if (that.type === 'jjb_small_video') {
        msg.title = that.details.desc
        msg.content = that.commonState.userInfo.nickname + '分享了@' + this.details.nickname + '的小视频，赶紧看看吧~'
      } else if (that.type === 'jjb_video') {
        msg.title = that.details.title
        msg.content = that.details.abstract
        msg.thumbs = [that.details.img_url ? this.$utils.exceptWebp(that.details.img_url) : `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}`]
      }
      if (ext) {
        msg.extra = {
          scene: ext
        }
      }
      let shareType = 1
      if (id === 'weixin') {
        if (ext === 'WXSceneTimeline') {
          shareType = 2
        } else {
          shareType = 1
        }
        // msg.type = 'web'
      } else {
        shareType = 1
      }
      console.log(shareType)
      if (id === 'sinaweibo') {
        msg.type = 'web'
        if (!this.share[id].nativeClient) {
          that.$toast({
            message: '请安装微博客户端',
            duration: 1500
          })
          return
        }
        this.share[id].authorize(function () {
          that.share[id].send(msg, function (res) {
            // 微信分享回调参数
            if (id === 'qq') {
              that.$toast({
                message: '分享成功',
                duration: 1500
              })
            }
            that.shareSuccess()
            that.closeShare()
          }, function (e) {
            if (e.code === -2) {
              that.$toast({
                message: '已取消分享',
                duration: 1500
              })
            } else {
              that.$toast({
                message: '分享失败',
                duration: 1500
              })
            }
            that.closeShare()
          })
        }, function (e) {
          that.$toast({
            message: '分享失败',
            duration: 1500
          })
        })
      } else {
        this.share[id].send(msg, function (res) {
          // 微信分享回调参数
          if (id === 'qq') {
            that.$toast({
              message: '分享成功',
              duration: 1500
            })
          }
          that.setShareParams()
          that.closeShare()
        }, function (e) {
          if (e.code === -2) {
            that.$toast({
              message: '已取消分享',
              duration: 1500
            })
          } else {
            that.$toast({
              message: '分享失败',
              duration: 1500
            })
          }
          that.closeShare()
        })
      }
    },
    // 复制链接
    getcopyLinkbtn () {
      this.copyflag = false
      if (!this.commonState.login_bol) {
        if (this.$utils.isWx()) {
          this.$utils.wxLogin()
        } else {
          this.$store.commit('common_login_box', true)
        }
        return
      }
      if (this.$utils.isWx()) {
        if (this.$route.query.inviteCode) {
          let shareSuccessObj = {}
          let shareArc = this.$utils.cache('shareArc') ? JSON.parse(this.$utils.cache('shareArc')) : null
          if (shareArc && shareArc.timestamp * 1 <= this.$utils.getTimeStamp()) {
            shareArc = null
            this.$utils.cache('shareArc', null)
          }
          if (!shareArc) {
            shareSuccessObj = {
              makeRate: 0.5,
              hasGet: 1256
            }
          } else {
            shareSuccessObj = {
              makeRate: shareArc.count * 0.5 + 0.5,
              hasGet: 1256
            }
          }
          this.set_shareSuccessObj({
            type: 2,
            shareSuccessObj
          })
          // 显示引导弹框
          this.set_shareType(2)
          this.setShowShare(true)
        } else {
          this.shareBol = true
        }
      } else {
        // 登录后重置一下复制链接拿到inviteCode
        // 显示qq分享引导
        if (Clipboard.isSupported()) {
          this.copyLink = this.$config.currentWebUrl + '?path=' + this.$route.path + '&inviteCode=' + this.commonState.userInfo.invitation
          let clb = new Clipboard('.copybutton')
          console.log(clb)
          clb.on('success',  (e) => {
            this.$toast({
              message: '复制链接成功了',
              duration: 1500
            })
            clb.destroy()
          })
          clb.on('error',  (e) => {
            this.$toast({
              message: '复制链接失败了',
              duration: 1500
            })
            clb.destroy()
          })
          this.copyflag = true
        }
      }
    },
    loadWxSignature () {
      let newUrl = ''
      if (location.href.indexOf('#') > -1) {
        newUrl = location.href.split('#')[0]
      } else {
        newUrl = ''
      }
      this.$api.post('WECHAT_SIGNATURE', {url: encodeURIComponent(newUrl), origin_url: location.origin}, (res) => {
        if (res.code === 0) {
          this.$api.get('USE_DOMAIN', { type: 'temp' }, (result) => {
            let newdomain = result.data.domain
            let sig = res.data
            this.pageUrl = newdomain + `?path=${this.$route.path}&inviteCode=${this.$utils.cache('inviteCode')}`
            let that = this
            let contentEl = document.createElement('div')
            contentEl.innerHTML = this.details.content
            let content = contentEl.innerText
            var shareContent = {
              title: this.details.title,
              desc: content, // 分享描述
              link: this.pageUrl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: this.details.img_url && this.details.img_url.length > 0 ? this.details.img_url[0] : `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}` // 分享图标
            }
            // 配置微信签名信息
            wx.config({
              debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
              appId: sig.appid, // 必填，公众号的唯一标识
              timestamp: sig.timestamp, // 必填，生成签名的时间戳
              nonceStr: sig.nonceStr, // 必填，生成签名的随机串
              signature: sig.signature, // 必填，签名
              jsApiList: sig.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
            })
            wx.ready(function () {
              // 分享到朋友圈
              wx.onMenuShareTimeline({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function (e) {
                  let temp = e.errMsg.split(':')
                  if (temp[1] === 'ok') {
                    that.shareSuccess()
                    that.$toast({
                      message: '分享成功',
                      duration: 1500
                    })
                  }
                  that.closeShare()
                }
              })
              // 分享到给好友
              wx.onMenuShareAppMessage({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function (e) {
                  let temp = e.errMsg.split(':')
                  if (temp[1] === 'ok') {
                    that.shareSuccess()
                  }
                  that.closeShare()
                }
              })
              // 分享到qq
              wx.onMenuShareQQ({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                  that.shareSuccess()
                  that.closeShare()
                },
                cancel: function () {
                  that.closeShare()
                }
              })
            })
            wx.error((res) => {
              console.log(res)
            })
          })
        }
      })
    },
    closeShare () {
      this.isShowShare = false
      this.wechatisshow = false
      this.appisshow = false
      this.$emit('closeShare')
    },
    getShortLink () {
      this.$api.get('SHORT_URL', {url: `${this.$config.currentWebUrl}?path=${this.$route.path}&inviteCode=${this.$store.state.common.userInfo.invitation}`}, res => {
        console.log(res)
        // this.shortLink = res.data.url
      })
    }
  },
  mounted () {
    const that = this
    this.shareComment() // 调用分享功能
    if (this.$utils.isWx()) {
      this.loadWxSignature()
    }
    this.getShortLink()
    var setAppShare = function () {
      that.share = {}
      window.plus.share.getServices(function (s) {
        for (let v of s) {
          that.share[v.id] = v
        }
      })
    }
    if (that.$config.source_style === 6 || that.$config.source_style === 7) {
      if (window.plus) {
        setAppShare()
      } else {
        document.addEventListener('plusready', function () {
          setAppShare()
        })
      }
    }
  },
  destroyed () {
    this.$emit('closeShare')
  }
}

</script>

<style lang="scss" scoped>
#shareIt{
  width: 100%;
  height: 100vh;
  height: 100%;
  .appShare {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 12;
    background: rgba($color: #000000, $alpha: .7);
  }
  .app-content{
    height: 693px;
    width: 719px;
    position: absolute;
    bottom: 14px;
    z-index: 1;
    margin-left: 15.5px;
    border-radius: 10px;
    background:#fff;
    // filter:alpha(opacity=30);
  .shareWayTop {
    width: 100%;
    height: 169px;
    background: url("../../assets/img/banner.png") no-repeat center center;
    background-size: 100% 169px;
    .viewbtn {
      width: 160px;
      height: 64px;
      background:url("../../assets/img/viewbtn.gif") ;
      background-size: 160px 64px;
      margin-left: 540px;
      position: absolute;
      top: 40px;
    }
  }
    .topshow{
      background: #fff;
      position: relative;
      border-radius: 10px;
      z-index: 999;
      .shareWay {
        height: 405px;
        margin-top: 33px;
      .icon {
        display: inline-block;
        text-align: center;
        .iconImg{
          width: 110px;
          height: 110px;
          position: relative;
          margin: 0 auto;
          .recommend_tag {
            position: absolute;
            top: -10px;
            right: -40px;
            width: 80px;
            height: 40px;
            background: url('../../assets/img/lockfan/recommend_tag.png') no-repeat left top;
            background-size: 100% 100%;
          }
          img{
            width: 100%;
            height: 100%;
          }
        }
        .name{
          margin-top: 15px;
          font-size: 30px;
          color: #333;
        }
        &:nth-child(1) {
          margin-left: 34px;
        }
        &:nth-child(n+1) {
          margin-left: 40px;
          margin-right: 10px;
        }
        &:nth-child(4n+1) {
          margin-left: 42px;
        }
        &:nth-child(5) {
          margin-top: 50px;
        }
        .copybutton {
          .iconImg {
            margin-left: 5px;
          }
        }
      }
    }
    .canlehid{
      height: 90px;
      border-top:1px solid #dbdbdb;
      box-sizing: border-box;
      background: #fff;
      text-align: center;
      color:#666666;
      position: absolute;
      width: 100%;
      line-height: 90px;
      border-radius: 10px;
    }
    }
  }
}
.wechat-cotent{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, .7);
  width: 100%;
  height: 100%;
  color: #fff;
  font-size: 34px;
  .jiantou{
    width: 206px;
    position: absolute;
    right: 30px;
    top:20px;
  }
  .wechat-icon-img {
    width: 110px;
    height: 110px;
  }
  .step1{
    padding:123px 0 75px 60px;
    display: flex;
    align-items: center;
    align-content: center;
    .dot-img{
      width: 70px;
      margin-left: 20px;
    }
  }
  .step2{
    padding-left: 110px;
    padding-bottom: 60px;
  }
  .wechat-icon{
    width: 484px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
}
</style>



