<template>
  <div id="videoDetails" class="hasVideoHeader" v-if="details">
    <!-- <div class="topTxt" v-if="$config.source_style === 6 || $config.source_style === 7"> -->
    <div class="topTxt">
      <div class="imgBox" @click="$router.push({path: '/'})">
        <img src="../../assets/img/black-left.png" alt="">
      </div>
      视频详情
      <div class="right shareIconBlack" v-if="$config.source_style === 6 || $config.source_style === 7" @click="change">
      </div>
    </div>
    <div v-if="$config.source_style === 6 || $config.source_style === 7"></div>
    <!-- 屏蔽了左右滑动事件 -->
    <!-- <v-touch class="abc" :class="{'pT90':($config.source_style === 6 || $config.source_style === 7)}" @swipeleft="onSwipeLeft" tag="div"> -->
     <div class="videoHeader" ref='videoHeader' v-if="showDetail && $config.source_style >= 6" :class="$config.source_style === 6 || $config.source_style === 7 ? 'hasVideoHeader' : ''">
     </div>
     <div class="videoHeader videoBrowser" v-if="showDetail && $config.source_style < 6" ref='videoHeader'>
       <img class="imageBg" :src="details.large_img_url" alt="">
       <div class="downloadInfo">
         <div class="d-title">下载掘金宝APP，即可观看完整视频</div>
         <div class="userInfo" v-if="articleList.length > 0">
           <div class="name">
             <div class="avatar"><img :src="articleList[0].avatar" alt=""></div>
             <div class="user-name">{{articleList[0].nickname}}</div>
           </div>
           <div class="tip">邀你和ta一起来赚掘金宝<br>看资讯影视，赚豪车洋房</div>
         </div>
         <div class="d-download" @click="toDownloadUrl">下载APP</div>
       </div>
     </div>
     <!-- 头部媒体信息 -->
     <div class="mediaInfo">
        <div class="video_title">{{details.title}}</div>
        <div class="play_count">{{details.video_watch_count | setNumber}}次播放</div>
        <div class="m_bottom_media">
          <div class="m_top">
            <div class="m_top_third tip">
              <div class="comment_icon" @click="setPlayButton('comment')">
                <img src="../../assets/img/video_detail_comment.png" alt="">
              </div>
              <div>{{total ? total : details.comment_count | setNumber}}</div>
            </div>
            <div class="m_top_second tip" :class="{active: details.is_fabulous === 1}">
              <div class="comment_icon" @click="setPlayButton('digg')">
              <!-- <div class="animation"></div> -->
              <img v-if="details.is_fabulous === 1" src="../../assets/img/video_list_digg_active.png" alt="">
              <img v-else src="../../assets/img/video_detail_digg.png" alt="">
              </div>
              <div>{{details.video_like_count | setNumber}}</div>
            </div>
             <div class="m_top_first tip clipbtn" @click="setPlayButton('share')" :data-clipboard-text="copyLink">
              <div class="comment_icon">
                <img src="../../assets/img/detail_share.png" alt="">
              </div>
              分享
            </div>
          </div>
       </div>
      <div class="m_bottom" :class="{isToggle: isToggle}">{{details.abstract}}</div>
       <span class="toggle" v-if="$utils.trim(details.abstract).replace(/[^\x00-\xff]/g, '01').length > 110" @click="setToggleShow">{{isToggle ?'收起':'展开'}}</span>
       <div class="m_bottom_media border-info">
         <div class="media_icon">
            <img :src="details.author_logo" alt="">
          </div>
          <div class="media_name">{{$utils.splitStr(details.author)}}</div>
       </div>
     </div>
      <!-- 文章内容 -->
      <div ref="detailsContent" v-if="details" :class="{'fullText':expandBol}">
        <!-- <div class="source clear f30 mT30">
          <div class="fl">来源：{{details.source}}</div>
        </div> -->
      </div>
      <!-- 展开全文 1000px -->
      <div class="expand clear"  @click="expandBtn" v-if="expandBol">
        <div class="fl txt">展开全文</div>
        <div class="fl img">
          <img src="../../assets/img/under.png" alt="">
        </div>
        <div class="fl box"></div>
      </div>
      <!-- <div class="clear recommend">
        <div class="fl f36 color50">
          精彩推荐
        </div>
      </div> -->
      <video-list
      :list="list"
      :type="1"
      @upDate="upDate"
      @isgoblack="isgoblack=false"
      class="wonderful bge"></video-list>
      <hot-comment @upperLevel="upperLevel" @getCopyLink="getCopyLink" @replyTotal='replyTotal' :shareCount="shareCount" :clickNum='clickNum' :details="details" :page="page" @pageOne="page=1" @total="getTotal" ></hot-comment>
      <!-- <loop-list class="bottomTip" v-if="articleList.length > 0 && $route.query.inviteCode" @showQQ='change' :list='articleList'></loop-list> -->
    <!-- </v-touch> -->
    <articleMd v-if="isShow" :coinParams="coinParams"></articleMd>
  </div>
</template>

<script>
import VideoList from '@/components/common/video-list'
import HotComment from '@/components/indexDetail/video-comment'
import HeaderNav from '@/components/common/header-nav'
import articleMd from '../common/article-md'
import loopList from '@/components/share/article_loop'
import Clipboard from 'clipboard'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'VideoDetail',
  computed: mapState({
    commonState: state => state.common,
    commonVideo: state => state.comment
  }),
  data () {
    return {
      showShare: false,
      isToggle: false,
      toggleShow: false,
      articleList: [],
      shareCount: 0,
      coinParams: {
        // coin: 50,
        // type: 'video'
      }, // 第一次阅读奖励参数
      clickNum: 0,
      copyLink: '',
      isPlay: false, // 是否播放状态的按钮
      msg: '文章详情',
      videoIsDigg: false,
      list: [],
      details: null,
      showDetail: false,
      isgoblack: true,     // 是否需要监听aid
      toScrollBol: false,  // 是否需要滚动到指定位置
      page: 1,             // 评论第几页
      total: 0,            // 评论总条数
      detailsArr: [],      // 存储跳转过的文章
      pageNum: 0,          // 可以分多少页
      expandBol: false,    // 是否有展开全文按钮
      oldHeight: true,     // 刷新文章是否保持滚动条回顶部
      timestamp: null,      // 时间戳
      isShow: false         // 首次阅读显示
    }
  },
  beforeRouteLeave (to, from, next) {
    for (let v of this.$refs.videoHeader.children) {
      v.parentNode.removeChild(v)
    }
    next()
  },
  methods: {
    ...mapMutations([
      'comment_video_detail',
      'set_shareSuccessObj',
      'setShowShare',
      'set_shareType',
      'common_login_box'
    ]),
    getShowShare (flag) {
      this.showShare = flag
    },
    setToggleShow () {
      this.isToggle = !this.isToggle
      // if (this.list[index].toggleShow) {
      //   this.$set(this.list[index], 'toggleShow', false)
      // } else {
      //   this.$set(this.list[index], 'toggleShow', true)
      // }
    },
    // 获取分享地址
    getCopyLink (val) {
      this.copyLink = val
    },
    toDownloadUrl () {
      window.open(this.$config.downloadUrl, '_blank')
    },
    replyTotal (e) {
      this.total = e
    },
    /**
     * @param {String} type play 播放按钮， digg 点赞， comment 评论按钮
     */
    setPlayButton (type) {
      switch (type) {
        case 'play':
          if (this.videoEl) {
            if (this.isPlay) {
              this.videoEl.pause()
              this.isPlay = false
            } else {
              this.videoEl.play()
              this.isPlay = true
            }
          }
          break
        case 'digg':
          if (!this.details.is_fabulous) {
            let data = {
              vid: this.$route.params.aid
            }
            this.$api.get('VIDEO_DIGG', data, res => {
              this.details.is_fabulous = 1
              this.$set(this.details, 'video_like_count', this.details.video_like_count ? ++this.details.video_like_count : 1)
              this.$toast({
                message: '点赞成功',
                duration: 1500
              })
            })
          }
          break
        case 'comment':
          var scrollHeight = this.$utils.getElposition(document.getElementById('hot-comment'))
          document.getElementById('app').scrollTop = scrollHeight
          scrollHeight -= 230
          if (this.$config.source_style >= 6) {
            // scrollHeight += 88
          }
          setTimeout(() => {
            window.scrollTo(0, scrollHeight)
          }, 200)
          break
        case 'share' :
          if (!this.commonState.login_bol) {
            return this.common_login_box(true)
          }
          if (this.$utils.isWx() || this.$config.source_style >= 6) {
            this.shareCount++
          } else {
            if (Clipboard.isSupported()) {
              let clb = new Clipboard('.clipbtn')
              clb.on('success',  (e) => {
                this.$toast({
                  message: '已复制文章链接,快发送给好友吧',
                  position: 'middle',
                  duration: 1500,
                  iconClass: 'icon icon-success'
                })
                clb.destroy()
              })
              clb.on('error',  (e) => {
                this.$toast({
                  message: '复制链接失败了',
                  position: 'middle',
                  duration: 1500,
                  iconClass: 'icon icon-success'
                })
                clb.destroy()
              })
            }
          }
          break
        default :
          console.log(type)
          break
      }
    },
    // 点击分享按钮
    change ({index, item}) {
      this.clickNum++
    },
    getContent () {
      if (this.articleList.length === 0 && this.$route.query.inviteCode) {
        this.$api.get('ARTICLE_LOOP_DATA', {invitation_code: this.$route.query.inviteCode}, res => {
          this.articleList = res.data
        })
      }
    },
    // 保存分享数据
    setShareParams () {
      let data = this.details
      data.videoid = data.content.id
      if (!data.videoid) return
      if (data._id) {
        delete data._id
      }
      data = {
        data: JSON.stringify(data),
        tag: 'jjb_video'
      }
      this.$api.post('VIDEO_STORE', data, res => {
      })
    },
    weixinPlay (fn) {
      if (!this.$utils.isWx()) {    // 非微信中直接执行，支不支持就交给浏览器本身了
        fn()
      } else {
        if (window.WeixinJSBridge) {
          window.WeixinJSBridge.invoke('getNetworkType', {}, fn)  // 这句话是在微信中可以自动播放的核心
        } else {
          document.addEventListener('WeixinJSBridgeReady', function () {
            window.WeixinJSBridge.invoke('getNetworkType', {}, fn)
          })
        }
      }
    },
    setVideoWatch () {
      let el = this.videoEl
      if (el) {
        this.$utils.setfullScreen(el[0])
      } else {
        this.$utils.setfullScreen(document.body)
      }
      this.videoEl.setAttribute('controlslist', 'nodownload')
      this.videoEl.setAttribute('autoplay', 'autoplay')
      this.weixinPlay(() => {
        this.videoEl.play()
      })
      this.videoEl.addEventListener('timeupdate', (e) => {
        e = e || window.event
        if (this.videoEl) {
          this.isPlay = !this.videoEl.paused
        } else {
          this.isPlay = false
        }
      })
      this.videoEl.play()
      this.videoEl.load()
    },
    // 返回上一级
    upperLevel () {
      if (this.$router.match.length <= 3) {
        this.$router.push('/')
      } else {
        this.$router.go(-1)
      }
    },
    // 点击展开全文
    expandBtn () {
      let h = this.$utils.getScrollTop()   // 记录滚动条高度
      this.expandBol = false
      this.$nextTick(() => {
        window.scrollTo(0, h)
      })
    },
    // 获取上拉列表
    upList () {
      if (this.commonState.isLoading === 0 && this.$utils.scrollHeight() < 200) {
        this.page++
      }
    },
    // 获取详情
    getDetails () {
      this.setReward()
      this.$store.commit('common_set_loading', 0)
      this.$api.get('VIDEO_DETAIL', {vid: this.$route.params.aid}, res => {
        this.details = res.data
        document.title = this.details.title
        if (this.oldHeight) {    // 刷新文章是否保持滚动条回顶部
          window.scrollTo(0, 0)
        }
        // this.setShareParams()
        this.oldHeight = true
        if (this.$config.source_style >= 6) {
          setTimeout(() => {
            this.showDetail = true
            this.$nextTick(() => {
              // 此次操作dom节点，由于v-if操作视频还是原来的缓存，所以直接使用js操作dom节点
              if (this.$refs.videoHeader.children[0]) {
                this.$refs.videoHeader.removeChild(this.$refs.videoHeader.children[0])
              }
              let div = document.createElement('div')
              div.setAttribute('tt-videoid', this.details.video_id)
              div.setAttribute('tt-poster', this.details.large_img_url)
              div.className = 'tt-video-box'
              div.innerText = '视频加载中...'
              let oScript = document.createElement('script')
              oScript.type = 'text/javascript'
              oScript.src = 'https://s0.pstatp.com/tt_player/tt.player.js?v=20160723'
              for (let v of this.$refs.videoHeader.children) {
                if (v.localName === 'script') {
                  v.parentNode.removeChild(v)
                }
              }
              this.$refs.videoHeader.appendChild(div)
              this.$refs.videoHeader.appendChild(oScript)
              // 获取video元素 100ms获取一次，获取到了就直接关闭定时器
              if (this.videoTimer) {
                clearInterval(this.videoTimer)
                this.videoTimer = null
              }
              let that = this
              this.videoTimer = setInterval(() => {
                if (this.videoEl) {
                  clearInterval(that.videoTimer)
                  this.videoTimer = null
                  this.setVideoWatch() // 设置播放监听事件
                  return false
                }
                this.videoEl = document.querySelector(`#${this.details.video_id}`)
              }, 100)
              if (this.$refs.detailsContent.offsetHeight > 1000) {
                this.expandBol = true
                this.isgoblack = true         // 是否需要监听aid
                if (this.toScrollBol) {
                  window.scrollTo(0, this.detailsArr[0].scroll)
                  this.toScrollBol = false
                  this.detailsArr.splice(0, 1)
                }
              }
            })
          }, 600)
        } else {
          this.showDetail = true
        }
      })
    },
    // 获取精彩推荐列表
    getRecommend () {
      this.$api.otherPost('VIDEO_RECOMMEND', {}, res => {
        if (res.data) {
          this.list = res.data.splice(0, 5)
        } else {
          this.list = []
        }
      })
    },
    // 是否关注
    isFollow () {
      let data = {}
      data.mid = this.details.mid * 1
      data.status = this.details.is_follow === 0 ? 1 : 2
      this.$api.otherPost('IS_FOLLOW', data, (res) => {
        this.details.is_follow = this.details.is_follow === 0 ? 1 : 0
        this.$utils.refreshAttention(data.status, data.mid)
      })
    },
    // 获取总评论条数
    getTotal (total) {
      this.total = total
      this.pageNum = Math.ceil(this.total / 10)
    },
    // 离开文章回调
    leave (aid) {
      if (this.$utils.cache('user_token')) {
        let data = {aid, starttime: this.timestamp, type: 'video'}
        this.$api.get('LEAVE', data, (res) => {
        })
      }
      // 清除定时器
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    },
    upDate () {
      let data = {}
      data.aid = this.$route.params.aid
      data.scroll = this.$utils.getScrollTop()
      this.detailsArr = [data, ...this.detailsArr]
      this.initData()
    },
    // 调取获得文章奖励的接口
    getReward () {
      if (this.$utils.isGetReard(this.$route.params.aid)) {
        this.$api.post('ARCREWARD_TIP', {aid: this.$route.params.aid, type: 'video'}, res => {
          if (res.data.coin > 0) {
            if (!this.$utils.cache('first_tips')) {
              this.coinParams = Object.assign({type: 'video'}, res.data)
              this.$utils.cache('first_tips', 1)
              this.isShow = true
            } else if (this.$config.source_style >= 6) {
              let shareSuccessObj = Object.assign({type: 1, tipText: `用掘金宝App看视频额外多赚了${res.data.extra_coin > 0 ? res.data.extra_coin : '更多'}金币`}, res.data)
              if (!this.$utils.cache('isFirstRead')) {
                this.set_shareSuccessObj({
                  type: 16,
                  shareSuccessObj
                })
                this.set_shareType(16)
                this.$utils.cache('isFirstRead', 1)
              } else {
                this.set_shareSuccessObj({
                  type: 7,
                  shareSuccessObj
                })
                this.set_shareType(7)
              }
              this.setShowShare(true)
            } else {
              let shareSuccessObj = Object.assign({type: 1, tipText: `用掘金宝App看视频赚更多金币`}, res.data)
              this.set_shareSuccessObj({
                type: 7,
                shareSuccessObj
              })
              this.set_shareType(7)
              this.setShowShare(true)
            }
            // 已获得奖励设置无奖励
            this.$utils.setArticle(this.$route.params.aid, 'article')
          }
        })
      }
    },
    // 设置奖励
    setReward () {
      this.count = 0
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
        return
      }
      this.timer = setInterval(() => {
        this.count++
        if (this.count >= 30) {
          this.getReward()
          clearInterval(this.timer)
          this.timer = null
        }
      }, 1000)
    },
    // 初始化
    initData () {
      this.getDetails()
      this.getRecommend()
      this.getContent()
    }
  },
  mounted () {
    this.timestamp = this.$utils.getTimeStamp()
    this.$utils.cache('rewardId', this.$route.params.aid)
    this.aid = this.$route.params.aid
    this.initData()
    window.removeEventListener('scroll', this.upList)
    window.addEventListener('scroll', this.upList)
  },
  watch: {
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (!this.commonState.login_box && this.commonState.login_bol) {
        this.oldHeight = false
        let h = this.$utils.getScrollTop()   // 记录滚动条高度
        this.initData()
        this.$nextTick(() => {
          window.scrollTo(0, h)
        })
      }
    },
    '$route.params.aid' (a, b) {
      this.leave(b)
      this.$utils.cache('rewardId', a)
      this.videoEl = null
      this.isPlay = false
      // 重置点赞状态
      this.timestamp = this.$utils.getTimeStamp()
      if (this.isgoblack) {
        this.toScrollBol = true
        this.initData()
      }
    }
  },
  destroyed () {
    this.leave(this.$utils.cache('rewardId'))
  },
  components: {
    VideoList,
    HotComment,
    HeaderNav,
    articleMd,
    loopList
  }
}
</script>
