      line-height: 50px;
      padding: 0 30px;
      font-size: 24px;
      color: #666;
      max-height: 76px;
      overflow: hidden;
      line-height: 40px;
      margin-bottom: 10px;
      display: -webkit-box;
      /*! autoprefixer: off */
      -webkit-box-orient: vertical;
      /*! autoprefixer: on */
      &.isToggle {
        max-height: 250px;
        min-width: 0;
        display: inline-block;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
    .toggle {
      background: #f4f5f7;
      color: #a2a2a2;
      display: inline-block;
      line-height: 45px;
      font-size: 24px;
      padding: 5px 20px;
      display: inline-block;
      margin-left: 28px;
    }
    .m_bottom_media{
      display: flex;
      align-items: center;
      padding: 10px 30px 20px;
      .media_icon{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-right: 20px;
        overflow: hidden;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
    .border-info {
      margin: 10px 0;
      border-top: 1px solid #f2f2f2;
      border-bottom: 1px solid #f2f2f2;
    }

  }
  .topTxt{
    line-height: 90px;
    background: #fff;
    color: #000;
    text-align: center;
    border-bottom:1px solid #eee;
    top: 0;
    position: fixed;
    width: 100%;
    z-index: 1;
    .imgBox{
      position: absolute;
      width: 18px;
      height: 32px;
      top: 50%;
      transform: translateY(-50%);
      left: 30px;
      line-height: 0;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .right{
      position: absolute;
      right: 0;
      top: 0;
      padding-right: 30px;
      padding-left: 20px;

    }
  }
  .fullText{
    height: 2000px;
    overflow: hidden;
  }
  .expand{
    text-align: center;
    margin: 20px;
    font-size: 28px;
    line-height: 36px;
    color: #5784df;
    position: relative;
    .box{
      position: absolute;
      width: 100%;
      height: 30px;
      background: linear-gradient(to top, #fff, rgba(255,255,255,0.3));;
      top: -50px;
    }
    .txt{
      margin-left: 290px;
    }
    .img{
      width: 18px;
      height: 12px;
      margin-left: 10px;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
  .abc{
    touch-action: pan-y !important;
  }
  .recommend{
    padding-left: 34px;
    display: flex;
    height: 100px;
    box-sizing: border-box;
    align-items: center;
    background: #fff;
    div{
      height: 100%;
      display: flex;
      height: 100px;
      box-sizing: border-box;
      align-items: center;
      border-bottom: 1px solid #FBAA03;
    }
  }
  .content{
    width: 100%;
    padding-left: 34px;
    padding-right: 22px;
    box-sizing: border-box;
  }
  .source{
    padding-left: 34px;
    color: #a3a3a3;
    line-height: 56px;
  }
  .bottomTip{
    bottom: 100px;
  }
}
</style>



