nt: center;\n      flex-flow: column wrap;\n      >div {\n        display: flex;\n        align-items: center;\n      }\n      .d-title {\n        height: 60px;\n      }\n      .d-download {\n        height: 60px;\n        width: 250px;\n        border-radius: 10px;\n        text-align: center;\n        justify-content: center;\n        margin-top: 20px;\n        background: #ff8b17;\n      }\n      .userInfo {\n        flex-flow: column wrap;\n        .name {\n          display: flex;\n          flex-flow: row nowrap;\n          align-items: center;\n          .user-name {\n            color: #ff8b17;\n          }\n          .avatar {\n            width: 70px;\n            height: 70px;\n            overflow: hidden;\n            margin-right: 10px;\n            border-radius: 50%;\n            img {\n              width: 100%;\n              height: 100%;\n            }\n          }\n        }\n        .tip {\n          margin-top: 10px;\n          line-height: 1.5;\n        }\n      }\n    }\n  }\n  .videoHeader{\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    z-index: 1;\n    zoom: 1;\n    height: 420px;\n    overflow: hidden;\n    background: #000;\n    text-align: center;\n    color: #999;\n    font-size: 28px;\n    line-height: 225px;\n    .tt-video-box{\n      height: 100%;\n      padding: 0;\n      video {\n        display: block;\n        height: 100%;\n      }\n    }\n  }\n  .videoHeader.hasVideoHeader{\n    top: 90px;\n  }\n  .mediaInfo {\n    .video_title {\n      line-height: 56px;\n      padding: 30px 30px 0px;\n      font-size: 34px;\n      font-weight: 600;\n      // background: #f5f5f5\n    }\n    .play_count {\n      font-size: 24px;\n      line-height: 60px;\n      padding: 0 34px;\n      color: #666\n    }\n    .m_top{\n      display: flex;\n      align-items: center;\n      font-size: 28px;\n      justify-content: space-between;\n      flex:1;\n      > div {\n        width: 210px!important;\n        overflow: hidden;\n      }\n      .tip{\n        display: flex;\n        align-items: center;\n        justify-content: center;\n        height: 60px;\n        width: 170px;\n        border-radius: 70px;\n        height: 70px;\n        font-size: 32px;\n        border: 1px solid #999;\n        text-align: right;\n        color: #666;\n        .comment_icon{\n          width: 39px;\n          height: 44px;\n          margin-right: 10px;\n          img{\n            width: 100%;\n            height: 100%;\n          }\n        }\n      }\n      .m_top_first {\n        .comment_icon {\n          width: 56px;\n          height: 50px;\n        }\n        .active {\n          width: 18px;\n          height: 39px;\n        }\n      }\n      .m_top_second {\n        .comment_icon {\n          width: 39px;\n          height: 42px;\n        }\n      }\n      .m_top_second.active{\n        color: #ed8778;\n      }\n      .m_top_third{\n        .comment_icon {\n          width: 48px;\n          height: 48px;\n        }\n        border-right: 0;\n      }\n    }\n    .m_bottom{\n      line-height: 50px;\n      padding: 0 30px;\n      font-size: 24px;\n      color: #666;\n      max-height: 76px;\n      overflow: hidden;\n      line-height: 40px;\n      margin-bottom: 10px;\n      display: -webkit-box;\n      /*! autoprefixer: off */\n      -webkit-box-orient: vertical;\n      /*! autoprefixer: on */\n      &.isToggle {\n        max-height: 250px;\n        min-width: 0;\n        display: inline-block;\n        overflow: hidden;\n        text-overflow: ellipsis;\n      }\n    }\n    .toggle {\n      background: #f4f5f7;\n      color: #a2a2a2;\n      display: inline-block;\n      line-height: 45px;\n      font-size: 24px;\n      padding: 5px 20px;\n      display: inline-block;\n      margin-left: 28px;\n    }\n    .m_bottom_media{\n      display: flex;\n      align-items: center;\n      padding: 10px 30px 20px;\n      .media_icon{\n        width: 50px;\n        height: 50px;\n        border-radius: 50%;\n  