<template>
  <swiper id="swiperList" class="swiper-container" :options="swiperOptions">
    <swiper-slide class="swiper-slide" v-for="(item, index) in list" v-if="item.goods" :key="index" @click.stop.native="setShowQQ(index)">
      <div class="header">
        <img :src="item.avatar" alt="头像">
      </div>
      <div class="rightContent">
        <span class="nickname">{{item.nickname}}</span>分享了这篇文章，即将赚取<span class="redColor">{{item.goods.shortname}}</span>，价值<span class="redColor">&yen;{{item.goods.price | setNumber}}！</span><span class="link">点击查看></span>
      </div>
    </swiper-slide>
    <div class="swiper-pagination" slot='pagination'></div>
  </swiper>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  props: {
    list: {
      type: Array
    }
  },
  computed: {
    
  },
  data () {
    return {
      swiperOptions: {
        autoplay: {
          delay: 5000
        },
        loop: true,
        clickable: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
          renderBullet: function (index, className) {
            return '<span class="' + className + '">' + '</span>'
          }
        }
      }
    }
  },
  methods: {
    setShowQQ (index) {
      let t = this.list[index]
      let obj = {
        brand_name: t.goods.brand_name,
        brand_logo: t.goods.brand_logo,
        shortname: t.goods.shortname,
        thumb: t.goods.pic_list_h[0],
        price: t.goods.price,
        avatar: t.avatar,
        nickname: t.nickname
      }
      this.$store.commit('set_shareSuccessObj', {
        type: 14,
        shareSuccessObj: obj
      })
      this.$emit('showQQ', {
        index,
        item: this.list[index]
      })
    }
  },
  filters: {
    setNumber (value) {
      return value > 10000 ? ((value / 10000).toFixed(2) + '万') : value
    }
  }
}
</script>
<style lang="scss" scoped>
  #swiperList{
    height: 130px;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 100px;
    z-index: 0;
    font-size: 28px;
    background: #fff;
    box-shadow: 0 -3px 10px rgba($color: #aaa, $alpha: .35);
    .swiper-slide{
      display: flex;
      padding: 0 30px 0 54px;
      height: 100%;
      box-sizing: border-box;
      justify-content: space-between;
      align-items: center;
      .header{
        width: 64px;
        height: 64px;
        overflow: hidden;
        border-radius: 50%;
        margin-right: 14px;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .rightContent{
        flex:1;
        min-width: 0;
        line-height: 1.5;
      }
    }
  }
</style>



