<template>
  <div id="hot-comment" ref="comment" v-if="$config.source_style >= 6">
    <!-- <div class="clear recommend pB10">
      <div class="fl pB20 f36 color50">
        热门评论
      </div>
    </div> -->
    <comment-list :list="list" :isVideo='1' @replyFn="replyFn"></comment-list>
    <transition name="">
      <dialog-bottom v-if="replyBol">
        <reply-list :isVideo='1' style="z-index: 10" :list="replyList"
        @getList="replyFn"
        :replyTotal="replyTotal"
        :isLoadingReply='isLoadingReply'
        @closeReply="replyBol=false"
        @addlist ="addlist"
        :item="replyItem"></reply-list>
      </dialog-bottom>
    </transition>
    <div class="footer clear" @touchmove="setMove" :class="{'positioning':!inputBol}" >
       <div class="fl oDiv1" @click="toIndex">
        <div class="backIcon">
          <img src="../../assets/img/detail_back.png" alt="">
        </div>
      </div>
      <label class="fl oDiv2" @click="setComment">
        <!-- <img src="../../assets/img/icon-input.png" alt=""> -->
        <div class="edit">
          <div class="edit-icon">
            <img src="../../assets/img/editIconGray.png" alt="">
          </div>
          <div class="txt">写评论...</div>
        </div>
      </label>
      <div class="fl oDiv3" @click="commentTarget">
        <img src="../../assets/img/detail_message.png" alt="">
        <div class="num" v-if="total!==0">{{total > 99 ? '99+' : total}}</div>
      </div>
      <!-- <div class="fl oDiv4" @click="collectionBnt">
        <img src="../../assets/img/my_collect.png" v-if="details.is_collection===0" alt="">
        <img src="../../assets/img/my_img3.png" v-if="details.is_collection!==0" alt="">
      </div> -->
      <div class="fl oDiv5 clipbtn" @click="change"  :data-clipboard-text="copyLink">
        <img src="../../assets/img/detail_share.png" alt="">
      </div>
    </div>
    <div class="comment clear" ref="maskBox" :class="{'positioning':inputBol}">
      <textarea
        id="comment"
        placeholder="优质评论将被优先展示..."
        class="fl"
        v-model="content"
        maxlength="100"
        ref="comments"
        @focus="inputFocus"
        @blur="inputBlur"></textarea>
      <label for="comment">
        <div class="fr oDiv pL30" v-if="content.length===0" @click="release">
          发布
        </div>
      </label>
      <div class="fr oDiv pL30 btnColor" v-if="content.length!==0" @click="release">
        发布
      </div>
      <!-- 评论遮罩层 -->
      <div class="mask" v-if="inputBol"></div>
    </div>
    <share-it v-if="shareBol" :type='"jjb_video"' @closeShare='closeShare' @shareSuccess="shareSuccess" ref="share" :details="details"></share-it>
  </div>
</template>

<script>
import commentList from '@/components/home/comment-list'
import shareIt from '@/components/indexDetail/share-it'
import replyList from '@/components/home/toutiao-reply-list'
import dialogBottom from '@/components/common/dialog-bottom'
// import config from './../../assets/js/config'
import Clipboard from 'clipboard'
// 导入wx 分享接口对象
import wx from 'weixin-js-sdk'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'hot-comment',
  props: {
    details: {
      type: Object,
      default: null
    },
    page: {                // 第几页
      type: Number,
      default: 1
    },
    clickNum: {
      type: Number,
      default: 0
    },
    shareCount: {
      type: Number,
      default: 0
    }
  },
  computed: mapState({
    commonState: state => state.common,
    login_bol: state => state.login_bol
  }),
  data () {
    this.copyLink = location.origin + '?path=' + this.$route.path + '&inviteCode=' + this.$utils.cache('inviteCode')
    this.$emit('getCopyLink', this.copyLink)
    return {
      setFocus: false,
      list: [],            // 评论列表
      per_page: 10,        // 一页多少条
      shareBol: false,     // 分享bol值
      replyBol: false,     // 是否打开回复上拉列表
      replyItem: null,     // 当前回复列表的那个对象
      replyList: [],       // 回复列表
      inputBol: false,     // 是否评论
      content: '',         // 评论内容
      total: 0,            // 总评论数
      pageBol: false,      // false位合并评论列表
      replyTotal: 0,        // 回复评论总数
      setdomain: '' // 配置中转页域名
    }
  },
  methods: {
    ...mapMutations([
      'common_login_box'
    ]),
    setMove (e) {
      e.target.preventDefault()
      return false
    },
    // 添加上拉加载评论
    addlist (list) {
      this.replyList = [...this.replyList, ...list]
    },
    // 评论定位
    commentTarget () {
      var scrollHeight = this.$utils.getElposition(document.getElementById('hot-comment'))
      document.getElementById('app').scrollTop = scrollHeight
      scrollHeight -= 230
      if (this.$config.source_style >= 6) {
        // scrollHeight += 88
      }
      setTimeout(() => {
        window.scrollTo(0, scrollHeight)
      }, 200)
    },
    // 发布评论
    release () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      if (!this.content.trim()) {
        // 空的话不做任何反应
        this.$toast({
          message: '请填写评论后发布',
          position: 'center',
          duration: 1500
        })
      } else if (this.content.length > 100) {
        this.$toast({
          message: '对不起，最多只能写100个字',
          position: 'bottom',
          duration: 1500
        })
      } else {
        let data = {}
        data.vid = this.$route.params.aid * 1
        data.content = this.content.trim()
        this.$api.post('SET_VIDEO_COMMENT', data, (res) => {
          this.list.unshift({
            cid: res.data.cid,
            avatar: this.commonState.userInfo.avatar,
            nickname: this.commonState.userInfo.nickname,
            reply: 0,
            comment_time: this.$utils.getTimeStamp(),
            content: data.content,
            fabulous: 0,
            is_fabulous: 0
          })
          ++this.total
          // 获取完数据以后进行滚动条定位
          this.$nextTick(() => {
            this.$toast({
              message: '评论成功',
              duration: 1500
            })
            this.commentTarget()
          })
          this.content = ''
        })
        this.pageBol = true
        // this.$emit('pageOne')
        // 获取完数据以后进行滚动条定位
        this.$nextTick(() => {
          this.commentTarget()
        })
        this.content = ''
      }
    },
    inputFocus () {
      this.inputBol = true
      let u = navigator.userAgent
      let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)  // ios终端 
      if (isiOS) {
        window.setTimeout(() => {
          window.scrollTo(0, document.body.clientHeight)
        }, 100)
      }
    },
    inputBlur () {
      this.inputBol = false
      this.commentTarget()
    },
    // 是否收藏
    collectionBnt () {
      let data  = {}
      data.aid = this.$route.params.aid * 1
      data.status = this.details.is_collection === 0 ? 1 : 2
      let txt = data.status === 2 ? '取消收藏成功' : '收藏成功'
      this.$api.post('CLLECT', data, (res) => {
        this.$toast({
          message: txt,
          position: 'middle',
          duration: 1500
        })
        this.details.is_collection = this.details.is_collection === 0 ? 1 : 0
      })
    },
    // 回复按钮
    replyFn (item) {
      if (!item.cid) return  // 无cid不做跳转
      this.replyList = []
      this.replyBol = true     // 打开回复列表
      let url = 'VIDEO_REPLY_LIST'
      this.replyItem = item
      if (this.isLoading) {
        return
      }
      this.isLoading = true
      this.isLoadingReply = 1
      this.$api.get(url, { cid: item.cid }, (res) => {
        if (res.data.top) {
          this.replyList = [...res.data.top, ...res.data.data]
        } else {
          this.replyList = res.data.data
        }
        this.isLoading = false
        this.isLoadingReply = 0
        this.replyTotal = res.data.total
      }, res => {
        this.isLoading = false
        this.isLoadingReply = 0
      })
      setTimeout(() => {
        this.isLoading = false
        this.isLoadingReply = 0
      }, 5000)
    },
    // 加载评论列表 ,传过去的aid:this.$route.params.mid
    getComments () {
      let data = {
        vid: this.$route.params.aid,
        page: this.page
      }
      this.$api.post('VIDEO_COMMENT', data, (res) => {
        this.list = res.data.data || [] // 填充数组
        this.total = res.data.total     // 总评论数
      })
    },
    // 回到首页
    toIndex () {
      this.$emit('upperLevel')
    },
    // 关闭分享组件
    closeShare () {
      this.shareBol = false
    },
    setShareParams () {
      // let data = this.details
      // data.videoid = data.id
      // if (!data.videoid) return
      // if (data._id) {
      //   delete data._id
      // }
      // data = {
      //   data: JSON.stringify(data),
      //   tag: 'jjb_video'
      // }
      // this.$api.post('VIDEO_STORE', data, res => {
      // })
    },
    // 加载微信签名
    loadWxSignature () {
      this.setShareParams()
      let newUrl = ''
      if (location.href.indexOf('#') > -1) {
        newUrl = location.href.split('#')[0]
      } else {
        newUrl = ''
      }
      this.$api.post('WECHAT_SIGNATURE', {url: encodeURIComponent(newUrl), origin_url: location.origin}, (res) => {
        if (res.code === 0) {
          this.$api.get('USE_DOMAIN', { type: 'temp' }, (res) => {
            let newdomain = res.data.domain
            let sig = res.data
            this.pageUrl = newdomain
            
            let that = this
            var shareContent = {
              title: this.details.title,
              desc: this.details.abstract, // 分享描述
              link: this.pageUrl + `?inviteCode=${this.$utils.cache('inviteCode')}&path=${this.$route.path}`, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: this.details.img_url && this.details.img_url.length > 0 ? this.details.img_url : `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}` // 分享图标
            }
            // 配置微信签名信息
            wx.config({
              debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
              appId: sig.appid, // 必填，公众号的唯一标识
              timestamp: sig.timestamp, // 必填，生成签名的时间戳
              nonceStr: sig.nonceStr, // 必填，生成签名的随机串
              signature: sig.signature, // 必填，签名
              jsApiList: sig.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
            })
            wx.ready(function () {
              // 分享到QQ空间
              wx.onMenuShareTimeline({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                  that.setShareParams()
                }
              })
              wx.onMenuShareAppMessage({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                  that.setShareParams()
                }
              })
              wx.onMenuShareQQ({
                title: shareContent.title, // 分享标题
                desc: shareContent.desc, // 分享描述
                link: shareContent.link, // 
                imgUrl: shareContent.imgUrl, // 分享图标
                success: function () {
                  that.setShareParams()
                }
              })
            })
            wx.error((res) => {
            })
          })
        }
      })
    },
    setComment () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.inputBol = true
      this.$refs.comments.focus()
      // 兼容iOS不能立即唤醒
      setTimeout(() => {
        this.$refs.comments.focus()
      }, 200)
    },
    change () {
      if (!this.commonState.login_bol) {
        if (this.$utils.isWx()) {
          this.$utils.wxLogin()
        } else {
          this.$store.commit('common_login_box', true)
        }
        return
      }
      // 点击分享时将数据传给后台，避免有时用户不回调导致分享详情没有
      if (this.$config.source_style === 6 || this.$config.source_style === 7) {
        this.shareBol = true
      } else {
        if (this.$utils.isWx()) {
          this.shareBol = true
        } else {
          if (Clipboard.isSupported()) {
            let clb = new Clipboard('.clipbtn')
            clb.on('success',  (e) => {
              this.$toast({
                message: '已复制文章链接,快发送给好友吧',
                position: 'middle',
                duration: 1500,
                iconClass: 'icon icon-success'
              })
              clb.destroy()
            })
            clb.on('error',  (e) => {
              this.$toast({
                message: '复制链接失败了',
                position: 'middle',
                duration: 1500,
                iconClass: 'icon icon-success'
              })
              clb.destroy()
            })
          }
        }
      }
    },
    // 初始化
    initData () {
      this.page = 1
      this.getComments()
      this.$refs.maskBox.addEventListener('touchmove', e => {
        e.preventDefault()
      }, {passive: false})
    }
  },
  mounted () {
    this.initData()
    this.loadWxSignature()
    this.inviteCode = this.$store.state.inviteCode
  },
  watch: {
    'details' () {
      this.initData()
    },
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (!this.commonState.login_box && this.commonState.login_bol) {
        this.initData()
      }
    },
    copyLink (val) {
      this.$emit('getCopyLink', val)
    },
    shareCount () {
      console.log(23)
      this.change()
    },
    total () {
      this.$emit('total', this.total)
    },
    page (p) {
      if (this.commonState.isLoading > 0) {
        return
      }
      this.$store.commit('common_set_loading', 1)
      let data = {
        vid: this.details.id,
        page: this.page
      }
      this.$api.post('VIDEO_COMMENT', data, (res) => {
        this.$store.commit('common_set_loading', 0)
        const lists = res.data.data || []
        this.list = [...this.list, ...lists]
        if (this.page >= res.data.last_page) {
          this.$store.commit('common_set_loading', 2)
        }
      })
    },
    clickNum () {
      this.change()
    },
    shareBol (flag) {
      this.$emit('getShowShare', flag)
    }
  },
  components: {
    commentList,
    shareIt,
    replyList,
    dialogBottom
  }
}
</script>

<style lang="scss" scoped>
#hot-comment{
  width: 100%;
  .recommend{
    padding-left: 21px;
    margin-top: 68px;
    div{
      border-bottom: 1px solid #FBAA03;
    }
  }
  .lala{
    width: 750px;
    height: 100%;
    background: #fff;
  }
  img{
    width: 100%;
    height: 100%;
  }
  .comment{
    border-top: 1px #DADADA solid;
    position: fixed;
    bottom: 5000px;
    height: 250px;
    background: #fff;
    width: 100%;
    z-index: 2000;
    .mask{
      width: 100%;
      height: 100vh;
      background: rgba(0,0,0,0.3);
      position: fixed;
      -webkit-overflow-scrolling: touch;
      z-index: 2;
      zoom: 1;
      bottom: 250px;
    }
    .oDiv{
      margin-right: 30px;
      margin-top: 10px;
      background: #959596;
      color: #fff;
      border-radius: 8px;
      padding: 10px 20px;
      text-align: center;
    }
    .btnColor{
      background: #E54256;
    }
    textarea:focus {
      outline: none;
      resize: none;
    }
    textarea {
      width: 690px;
      height: 160px;
      margin-top: 20px;
      margin-left: 28px;
      border-radius: 20px;
      box-sizing: border-box;
      border: none;
      font-size: 30px;
      text-indent: 10px;
      padding: 25px;
      background: #F0F2F1;
    }
    textarea::-webkit-input-placeholder {
      color: #afafaf;
      font-size: 30px;
    }
  }
  .footer{
    border-top: 1px #DADADA solid;
    position: fixed;
    bottom: -1000px;
    height: 100px;
    z-index: 10000;
    background: #fff;
    width: 100%;
    line-height: 100px;
    z-index: 0;
    .oDiv5{
      width: 56px;
      height: 48px;
      padding-top: 8px;
      padding-left: 15px;
    }
    .oDiv3,.oDiv4{
      position: relative;
      width: 48px;
      height: 48px;
      margin-left: 25px;
      margin-right: 10px;
      padding-top: 10px;
      .num{
        position: absolute;
        top: 15px;
        left: 60%;
        font-size: 16px;
        padding: 5px;
        background: red;
        line-height: 18px;
        padding-left: 8px;
        padding-right: 8px;
        color: #fff;
        border-radius: 13px;
      }
    }
    .oDiv2{
      width: 480px;
      height: 60px;
      padding-top: 18px;
      padding-right: 10px;
      position: relative;
      .edit{
        display: flex;
        align-items: center;
        height: 70px;
        border-radius: 35px;
        background: #f5f5f5;
        overflow: hidden;
        .edit-icon{
          width: 38px;
          height: 35px;
          position: relative;
          display: flex;
          align-items: center;
          justify-items: center;
          margin: 0 17px 0 34px;
          img{
            width: 100%;
            height: 100%;
            line-height: 0;
          }
        }
        .txt{
          color: #666;
          font-size: 24px;
        }
      }
     
    }
    .oDiv1{
      display: flex;
      align-items: center;
      padding: 0 20px 0 30px;
      height: 100%;
      .backIcon{
        width: 18px;
        display: flex;
        padding: 10px;
        margin-left: -10px;
        margin-right: -10px;
        align-items: center;
        height: 35px;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .positioning{
    bottom: 0;
  }
}
</style>



